import * as constants from "./../constants/constants"

export const requestMessages = (url) =>{
    return dispatch => {
        dispatch(request(constants.REQUEST_MESSAGES))
        return fetch(url)
          .then(response => response.text())
          .then(text => dispatch(receiveText(text, constants.RECEIVE_MESSAGES)))
      }
  };

  export const request = (constantType) => {
    return {
      type: constantType
    };
  };

  export const receiveText = (text, constantType) => {
    return {
      type: constantType,
      body: text,
      receivedAt: Date.now()
    }
  };

  export const parsed = (messages) => {
    return{
      type: constants.PARSED,
      body: messages
    }
  }

  export const scrolled = () => {
    return {
      type: constants.SCROLLED
    }
  }

  export const swap = () => {
    return {
      type: constants.SWAP
    }
  }


