import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import WhatsappChat from "../components/WhatsappChat-component";

import * as actions from "../actions/actions";

const mapStateToProps = state => {
    const current_user = state.whatsapp.messages[state.whatsapp.messages.length - 2].sender
    return {
        messages: state.whatsapp.messages,
        current_user: current_user,
        messages_changed: state.whatsapp.messages_changed
    };
};

const mapDispatch = dispatch => {
  return {
    actions: bindActionCreators(actions, dispatch)
  };
};

export default connect(mapStateToProps, mapDispatch)(WhatsappChat);



