import * as constants from "./../constants/constants"

const initialState = {
    messages: [{
        message: 'hello',
        sender: 'John Doe',
        date: '12-13-1444',
        time:'12:20',
        float: 'left',
        index: 0,
        new_date: true
    },
    {
        message: 'hey',
        sender: 'Eve',
        date: '12-13-1444',
        time:'12:22',
        float: 'right',
        index: 1
    }],
    messages_changed: false,
    current_user: 'none'
};

const parseText = (text) => {
    let current_user = 'none'
    let other_user = 'none'
    let previous_date = 'none'
    let previous_user = 'none'
    let previous_time = 'none'

    return text.split('\n').map((line, index) =>
    {
        
        let newDate = false
        const containsTime = line.substring(0, line.indexOf(': '))
        const timeSeperator = containsTime.indexOf(':')
        const sender = line.substring(
            line.indexOf(' - ') + 3,
            line.indexOf(': ')
        )
        if (index === 0 ){
            current_user = sender
        }
        if (other_user === 'none' && sender.indexOf(current_user) === -1){
            other_user = sender
        }

        if (other_user !== 'none' && current_user !== other_user) {
            if (line.indexOf(current_user + ':') === -1 && line.indexOf(other_user + ':') === -1) {
                return {
                    message: line,
                    date: previous_date,
                    float: sender === previous_user ? 'right' : 'left',
                    time: previous_time,
                    sender: previous_user,
                    index: index,
                    new_date: false
                }
            }
        }

        const time = containsTime.substring(
            timeSeperator - 2,
            timeSeperator + 3
        )
        const date = timeSeperator === 2 ? 
        containsTime.substring(
            6,
            containsTime.indexOf(' -')
        )
        :
        containsTime.substring(
            0, 8
        )
        if (date !== previous_date){
            previous_date = date
            newDate = true
        }

        previous_user = sender
        previous_time = time

        return {
            message: line.substring(
                line.indexOf(sender) + sender.length + 1
            ),
            date: date
            ,
            time: time,
            sender: sender,
            float: sender === current_user ? 'right' : 'left',
            index: index,
            new_date: newDate
        }
    })
}

const whatsappChatReducer = (state = initialState, action) => {
    switch (action.type) {
      case constants.RECEIVE_MESSAGES:{
        const messages = parseText(action.body)
        return{
          ...state,
          messages: messages,
          messages_changed: true,
          current_user: messages[0].sender
        }
      }
      case constants.SCROLLED:{
        return{
            ...state,
            messages_changed: false
          }
      }
      case constants.SWAP:{
        const _message = state.messages.find(message => {
            return message.sender !== state.current_user
        })

        const current_user = _message.sender
        
        return{
            ...state,
            current_user: current_user,
            messages: state.messages.map(message =>
            {
                return{
                    ...message,
                    float: message.sender === current_user ? 'right' : 'left'
                }
            })
          }
      }
      default: {
        return {
          ...state
        };
      } 
    }
  };
  
  export default whatsappChatReducer;
  


