import React, { Component } from 'react';
import List from 'material-ui/List';
import Button from '@material-ui/core/Button';
import ReactDOM from 'react-dom';

export default class Days extends Component {
    constructor(props){
        super(props)
        this.state = {
            show: 10000,
            current: 1,
            up: true,
            update: true
        }
    }

    getDays = () => {
        const result = []
        const dates = []
        const BreakException = {};
        try{
            this.props.days.forEach((day, index) => {
                if (result.length > this.state.show) { 
                    throw BreakException
                }
                if (result.indexOf(day) === -1){
                    dates.push(
                        {
                            date: day, 
                            index: index
                        })
                    result.push(day)
                }

            });
        }
        catch (e) {
            if (e !== BreakException) throw e;
        }
        return dates
    }

    handleClick = (index) => {
        if (index !== 0){
             index--
        }
        this.props.setShow(this.props.total - index)
        this.setState({
            update: false
        })
    }

    addDays = (number, up = true) =>{
        let show = this.state.show + number
        if (show >= this.props.days.length){
            show = this.props.days.length - 1
        }
        if (show > 0){
        this.setState(
            {
                show: show,
                up: up
            }
        )
    }
    }
    componentDidMount() {
        ReactDOM.findDOMNode(this.refs["Days_ref"]).parentNode.addEventListener('scroll', (e)=>{
            const tableBodyNode = ReactDOM.findDOMNode(this.refs["Days_ref"]);
            const scroll = tableBodyNode.parentNode.scrollHeight - tableBodyNode.parentNode.scrollTop - tableBodyNode.parentNode.clientHeight
            if (scroll === 0) {
                this.addDays(30)
            }
        });
     }

    componentDidUpdate(){
        if (this.state.update) {
            ReactDOM.findDOMNode(this.refs["days_bottom"]).scrollIntoView()
        }
    }

    render() {
    const days = this.getDays()
      return (
          <div>
            <List ref="Days_ref" className="days_container">
                {
                    days
                    .map((day, index) => 
                    <Button variant="raised" 
                    key={index}
                    style={{
                        margin: '5px'
                    }}
                    onClick={() => this.handleClick(day.index)}
                    >
                            {day.date}
                    </Button>
                    )
                }
                <div style={{ float:"left", clear: "both" }} ref="days_bottom" />
            </List>
        </div>
      );
    }
  }


