import React, { Component } from 'react';
import PropTypes from "prop-types";
import ReactDOM from 'react-dom';

import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import List from 'material-ui/List';
import Button from '@material-ui/core/Button';

import Message from './Message-component'
import SearchComponent from './Search-component'
import DialogComponent from './Dialog-component'
import Days from './Days-component'

import Dropzone from 'react-dropzone'

export default class WhatsappChat extends Component {
    constructor(props){
        super(props)
        this.state = {
            show: 30,
            up: true,
            date: false,
            dialog: false,
            message: ''
        }
        this.messagerefs = {}
        this.search_messages = []
        this.current_search = 0
        this.previous_search_value = ''
    }
    static propTypes = {
      messages: PropTypes.array,
      messages_changed: PropTypes.bool
    };
    
    onDrop = (files) => {
        files.forEach(file => {
            if (file.name.indexOf('.txt') === -1){
                alert("Please enter a text file")
            }
            else{
                this.props.actions.requestMessages(file.preview)
                this.messagerefs = {}
                this.search_messages = []
                this.current_search = 0
                this.previous_search_value = ''
                this.setState({
                    show: 30,
                    up: true,
                    date: false,
                    dialog: false
                })
            }
        });
    }
    
    search = (value) => {
        this.search_messages = this.props.messages.filter((message) => {
            return message.message.toLowerCase().indexOf(value.toLowerCase()) !== -1
        })
        if (this.previous_search_value !== value){
            this.previous_search_value = value
            this.current_search = 0
        }
        this.next_search()
    }

    next_search = () => {
        if (this.search_messages.length === 0) {
            this.setState({
                dialog: true,
                message: 'Message not found'
            })
        }
        else if (this.current_search >= this.search_messages.length){
            this.current_search = 0
            this.setState({
                dialog: true,
                message: 'Max search result'
            })
            //this.setShow(this.props.messages.length - this.search_messages[this.search_messages.length - this.current_search - 1].index)
        }
        else if (this.search_messages.length > 0) {
            this.setShow(this.props.messages.length - this.search_messages[this.search_messages.length - this.current_search - 1].index)
            this.current_search += 1
        }
        else{
            this.setState({
                dialog: false
            })
        }
    }

    setShow = (show) => {
        if (this.props.messages.length < 30) {
            show = this.props.messages.length
        }
        this.setState({
            dialog: false,
            show: show,
            date: true
        })
    }

    addMessages = (number, up = true) =>{
        let show = this.state.show + number
        if (show >= this.props.messages.length){
            show = this.props.messages.length - 1
        }
        if (show > 0){
        this.setState(
            {
                dialog: false,
                show: show,
                up: up,
                date: false
            }
        )
    }
    }

    componentDidUpdate(){
        if (this.state.date){
            if (this.state.show < 10 && this.props.messages.length > 30){
                this.setState({
                    show: 30,
                    date: false,
                    up: true,
                })
            }
            else{
                ReactDOM.findDOMNode(this.refs["message_" + String(this.props.messages.length - this.state.show)]).scrollIntoView()
            }
        }
        else if (!this.state.up){
            try {
            ReactDOM.findDOMNode(this.refs["message_" + String(this.props.messages.length - (this.state.show) + 12 )]).scrollIntoView()
            }
            catch (e){
                console.log("message not found")
            }
        }
        else if (this.state.show > 30) {
            ReactDOM.findDOMNode(this.refs["message_" + String(this.props.messages.length - (this.state.show - 30) )]).scrollIntoView()
        }
        else{
            ReactDOM.findDOMNode(this.refs["chat_bottom"]).scrollIntoView()
        }
    } 

    onScroll = () => {
        // if (this.search_messages.length > 0 && this.search_messages[this.search_messages.length - this.current_search].index > this.props.messages.length - 7)
        // {
        //     return
        // }

        const tableBodyNode = ReactDOM.findDOMNode(this.refs["WhatsappChat-ref"]);
        if (tableBodyNode.scrollTop === 0 && this.props.messages.length > this.state.show){
            this.addMessages(30)
        }
        if (tableBodyNode.scrollHeight - tableBodyNode.scrollTop - tableBodyNode.clientHeight === 0) {
            this.addMessages(-30, false)
        }
    }

    swap = () => {
        this.props.actions.swap()
    }

    render() {
        const show = this.props.messages.length - this.state.show
        const Fragment = React.Fragment;
        return (
        <div>

            {this.state.dialog ? <DialogComponent show={this.state.dialog} message={this.state.message} /> : null}

            <List className="WhatsappChat" ref="WhatsappChat-ref" onScroll={this.onScroll}>
        
                <div className="chat">
                    {
                        this.props.messages.slice(show,
                            show + 50)
                            .map((message, index) =>
                        <div key = {index}>
                        {message.new_date ?
                        <div className="date_container">
                            <Typography  color="textSecondary" className="date">
                                {message.date}
                            </Typography>
                        </div>
                        : null
                        }
                        {
                        message.message !== '' ?
                        <Message
                            key = {index}
                            date = {message.date} 
                            message = {message.message}
                            sender = {message.sender}
                            time = {message.time}
                            ref = {`message_${message.index}`}
                            float = {message.float}
                            className = "Message"
                            index = {message.index}
                            highlight = {this.previous_search_value}
                        />
                        :
                        null
                        }
                        </div>
                    )
                }

                 <div style={{ float:"left", clear: "both" }} ref="chat_bottom" />
                </div>
            </List>

            <div className="swapContainer">
                <Button variant="raised"
                onClick={this.swap}
                className="swapButton"
                style={{
                    marginTop: '15px',
                    backgroundColor: 'rgba(0, 0, 0, 0.13)',
                }}
                >
                    Swap
                </Button>
            
            </div>

            <div className="SearchBar">
                <SearchComponent search={this.search} next_search={this.next_search}/>
                
                <Typography  color="textSecondary" className="searchInfo">
                        {this.search_messages.length > 0 ?
                        <Fragment>
                        <Fragment>{this.search_messages.length} search result(s)</Fragment>
                        <br/>
                        <Fragment>{this.current_search === 0 ? this.search_messages.length: this.current_search} / {this.search_messages.length}</Fragment>
                        </Fragment>
                        :
                        <Fragment>Press enter to search / next item</Fragment>
                        }
                </Typography>
            </div>
            

            <div className="chatBar">
                <Dropzone onDrop={this.onDrop} className='dropzone'>
                        <Card className="Card">
                            <CardContent>
                                    <Typography variant="title" component="h3" className="desktop_upload">
                                        Upload or drop whatsapp backup text file here
                                    </Typography>
                                    <Typography variant="title" component="h3" className="mobile_upload">
                                        Upload
                                    </Typography>
                            </CardContent>
                        </Card>
                </Dropzone>

                <Days days={this.props.messages.map(message => message.date)} setShow={this.setShow} total={this.props.messages.length}/>

            </div>
        </div>
        );
        }
  }


